import React from 'react';
import Slide from "./slide";

let content = [{name: 'Javascript', rate: 9}, {name: 'Html', rate: 8}, {name: 'Css', rate: 9}, {
    name: 'React',
    rate: 8
}, {name: 'Redux', rate: 8}, {name: 'Nodejs', rate: 8}, {name: 'Bootstrap', rate: 9}]

function AboutContent(props) {
    return (
        <section className='d-flex justify-content-center flex-column aboutContainer' style={{height: '100vh'}}>
            <h3 className='mb-4 d-flex ml-2'>{props.name}</h3>
            <ul className='list-group list-group-flush col-6'>
                {props.content.map((item) => <li className='list-group-item  d-flex justify-content-between align-items-center'
                                                 style={{width: '100%'}}><i>{item.name}</i>
                    <span className="badge badge-primary badge-pill ml-4">{item.rate}</span></li>)}
            </ul>
        </section>

    )
}

function AboutSlide(props) {
    return (
        <Slide imageAlign={props.data.align} image={props.data.src} content={function () {
            return <AboutContent name='Skills' content={content}/>
        }}/>
    )
}

export default AboutSlide;